.pokemon-details {
  max-width: 600px;
  margin: 20px auto;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 2px 15px rgba(0, 0, 0, 0.1);
  color: #333;
  font-family: "Arial", sans-serif;
}

.pokemon-details h2 {
  color: #444;
  text-align: center;
}

.pokemon-details img {
  display: block;
  margin: 0 auto 20px;
  height: 150px;
}

.pokemon-details p {
  margin: 10px 0;
  font-size: 16px;
}

.pokemon-details a {
  display: inline-block;
  margin-bottom: 20px;
  color: #666;
  text-decoration: none;
  background-color: #e2e2e2;
  padding: 10px 15px;
  border-radius: 5px;
}

.pokemon-details a:hover {
  color: #333;
  background-color: #d2d2d2;
}
